/* - link under - */

.link-arrow:after {
    content: '';
    background-size: 100% 100%;
    width: 16px;
    height: 12px;
    position: absolute;
    top: 5px;
    right: -28px;
    -webkit-transition: all .4s cubic-bezier(.35, 1, .33, 1);
    transition: all .4s cubic-bezier(.35, 1, .33, 1)
}

.link-arrow:hover:after {
    right: -35px
}

.link-arrow.link-arrow-white:after {
    background-size: 100% 100%
}

.link-arrow-hover:after {
    right: -35px
}

.link {
    display: inline-block;
    position: relative;
    border: none;
    padding-bottom: 4px;
    text-transform: uppercase;
    font-family: Montserrat, helvetica, arial, sans-serif;
    font-weight: 700
}

.link:active, .link:focus, .link:hover {
    outline: 0
}

.link:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    background: #000;
    width: 0;
    height: 2px;
    -webkit-transition: all .4s cubic-bezier(.35, 1, .33, 1);
    transition: all .4s cubic-bezier(.35, 1, .33, 1)
}

.link:hover {
    color: #FFF;
}

.link:hover:before {
    width: 100%
}

.link-theme:before {
    background: #ffd300
}

