

.gradient-multiline {
  position: relative;
  width: 100%;
  text-align: center;
  line-height: 1.5em;
  overflow: hidden;
  /* transform: rotate(-10deg); */
}

.gradient-multiline span {
  color: #fff;
  background-color: #000;
  padding: 0.225rem 0.5rem;
  /* Needs prefixing */
  -webkit-box-decoration-break: clone;
  box-decoration-break: clone;
}

@supports (mix-blend-mode: lighten) {
  .gradient-multiline::after {
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    pointer-events: none;
    background: linear-gradient(60deg, #3629a2, #00ceff);
    mix-blend-mode: lighten;
  }
}

/* Reservation Form */

body {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 15px;
}

h4 {
  color: #00ceff;
}

input, input[type="radio"]+label, input[type="checkbox"]+label:before, select option, select {
  width: 100%;
  padding: 1em;
  line-height: 1.4;
  background-color: #f9f9f9;
  border: 1px solid #e5e5e5;
  border-radius: 3px;
  transition: 0.35s ease-in-out;
  transition: all 0.35s ease-in-out;
}

input:focus {
  outline: 0;
  border-color: #3629a2;
}

input:focus+.input-icon i {
  color: #3629a2;
}

input:focus+.input-icon:after {
  border-right-color: #3629a2;
}


select {
  height: 3.4em;
  line-height: 2;
}

select:first-of-type {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}

select:last-of-type {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}

select:focus, select:active {
  outline: 0;
}

select option {
  background-color: #7ed321;
  color: #fff;
}

.input-group {
  margin-bottom: 1em;
  zoom: 1;
}

.input-group:before, .input-group:after {
  content: "";
  display: table;
}

.input-group:after {
  clear: both;
}

.input-group-icon {
  position: relative;
}

.input-group-icon input {
  padding-left: 4.4em;
}

.input-group-icon .input-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 3.4em;
  height: 3.4em;
  line-height: 3.4em;
  text-align: center;
  pointer-events: none;
}

.input-group-icon .input-icon:after {
  position: absolute;
  top: 0.6em;
  bottom: 0.6em;
  left: 3.4em;
  display: block;
  border-right: 1px solid #e5e5e5;
  content: "";
  -webkit-transition: 0.35s ease-in-out;
  -moz-transition: 0.35s ease-in-out;
  -o-transition: 0.35s ease-in-out;
  transition: 0.35s ease-in-out;
  transition: all 0.35s ease-in-out;
}

.input-group-icon .input-icon i {
  -webkit-transition: 0.35s ease-in-out;
  -moz-transition: 0.35s ease-in-out;
  -o-transition: 0.35s ease-in-out;
  transition: 0.35s ease-in-out;
  transition: all 0.35s ease-in-out;
}

.container {
  max-width: 38em;
  line-height: 1.1;
  padding: 1em 3em 2em 3em;
  margin: 0em auto;
  background-color: #fff;
  border-radius: 4.2px;
  box-shadow: 0px 3px 10px -2px rgba(0, 0, 0, 0.2);
}

.row {
  zoom: 1;
}

.row:before, .row:after {
  content: "";
  display: table;
}

.row:after {
  clear: both;
}

.col-half {
  padding-right: 10px;
  float: left;
  width: 33%;
}

.col-half:last-of-type {
  padding-right: 0;
}

.col-third {
  padding-right: 10px;
  float: left;
  width: 33.33333333%;
}

.col-third:last-of-type {
  padding-right: 0;
}

@media only screen and (max-width: 540px) {
  .col-half {
    width: 100%;
    padding-right: 0;
  }
}

.ant-calendar-picker-input.ant-input {
  height: 53px;
}

.ant-input-number-lg {
  height: 53px;
  font-size: 18px;
}

.ant-time-picker-input:placeholder-shown{
  height: 53px;
}

input:not([type='range']) {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  height: 52px;
}

.form-error-msg{
  color: red;
  margin: 0;
  font-size: 12px;
}

.go-back-btn{
  margin: 20px;
}